<template>
  <div class="long-text-page">
    <!--   -->
    <div class="doc-tit-box">
      <div class="doc-tit-content"></div>
    </div>
    <!--  -->
    <div class="content-box">
      <!--  -->
      <div class="key-word-list">
        <div class="tab-nav"></div>
        <ul class="text-list">
          <p class="no-key-tips" v-if="data.catalogue.length == 0">
            
          </p>
          <li
            class="text-list-item"
            v-for="item in data.catalogue"
            :key="item.id"
            v-html="item.content"
            @click="switchHighlight($event)"
          ></li>
        </ul>
      </div>
      <!--  -->
      <div class="long-text-box">
        <div class="loading"  v-show="data.isLoading">
          <p class="loading-icon iconfont icon-sousuo"></p>
          <p id="LoadAnimation"></p>
        </div>
        <!--  -->
        <div class="text-title-box">
          <input
            class="query-input"
            type="text"
            placeholder=""
            v-model="data.queryKeyWord"
            @keydown.enter="startQuery"
          />
          <p class="query-btn iconfont icon-sousuo" @click="startQuery"></p>
          <!--  -->
          <div class="querytype">
            <input
              class="checkbox-type"
              id="t1"
              type="checkbox"
              @click="
                data.queryType =
                  data.queryType === data.RETRIEVAL_TYPE.paragraph
                    ? data.RETRIEVAL_TYPE.piece
                    : data.RETRIEVAL_TYPE.paragraph
              "
              :checked="data.queryType === data.RETRIEVAL_TYPE.paragraph"
            />
            <label for="t1"></label>
            <input
              class="checkbox-type"
              id="t2"
              type="checkbox"
              @click="data.queryType = data.queryType === '2' ? '0' : '2'"
              :checked="data.queryType === '2'"
            />
            <label for="t2"></label>
          </div>
          <!--  -->
          <div class="key-word-position">
            <span
              class="iconfont icon-qiehuanqizuo"
              @click="switchHighlight('prev')"
            ></span>
            <span>{{ data.Knum }} / {{ data.Ktotal }}</span>
            <span
              class="iconfont icon-qiehuanqiyou"
              @click="switchHighlight('next')"
            ></span>
          </div>
        </div>

        <!--  -->
        <div class="long-text">
          <div
            class="center"
            id="longCenter"
            v-html="data.longTextContent"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, getCurrentInstance, onMounted } from 'vue';
import Clipboard from 'clipboard';
import { RETRIEVAL_TYPE } from './longTextConst.js';
// import { swh1 } from './minText.js';
// import { swh1 } from './LongText.js';
import { swh1 } from './bigText.js';

export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      RETRIEVAL_TYPE,
      keyWordBCol: 'red', //
      keyWordSCol: '#fff', //
      oldEle: null, //
      Knum: 0, //
      Ktotal: 0, //
      listNumId: 0, //id

      textCenter: '', // ()
      longTextContent: '', // ()
      centerEle: null, // dom

      queryKeyWord: '   ', //      
      oldQueryKeyWordReg: '', //
      queryKeyWordArr: [], //
      queryKeyWordReg: '', //      |
      keyWordIndex: [], // 
      catalogue: [], // 
      queryType: RETRIEVAL_TYPE.piece, // 0  1  2
      oldQueryType: '', // 
      keyPLength: 30, //

      copyButton: null, //
      isLoading: false, //
      isHaveKey: false //
    });
    // 
    onMounted(() => {
      data.centerEle = document.getElementById('longCenter'); // dom
      data.textCenter = data.centerEle.innerText; // 
      copyTable(); //
    });
    // 
    const copyTable = () => {
      // table
      let tableEleAll = Array.from(document.getElementsByTagName('table'));
      // table
      tableEleAll.forEach((itemTable, index) => {
        // 
        itemTable.onmouseenter = function (e) {
          data.copyButton = document.createElement('button'); //
          data.copyButton.className = 'copy';
          data.copyButton.innerHTML = '';
          const currentEle = e.target; //
          currentEle.appendChild(data.copyButton); //
          // 
          data.copyButton.onclick = new Clipboard('.copy', {
            // target
            target: function () {
              //
              itemTable.removeChild(itemTable.lastChild);
              // tableword
              currentEle.width = '98%';
              return currentEle;
            }
          });
          //
          data.copyButton.onclick.on('success', function (e) {
            emptySelectText();
            // 
            myTips('success', '~');
            data.copyButton.onclick.destroy();
          });
          // 
          data.copyButton.onclick.on('error', function (e) {
            emptySelectText();
            // 
            myTips('error', '~');
            data.copyButton.onclick.destroy();
          });
        };
        // 
        itemTable.onmouseleave = function () {
          data.copyButton.onclick.destroy();
          itemTable.removeChild(itemTable.lastChild);
        };
      });
    };
    // 
    const emptySelectText = () => {
      window.getSelection
        ? window.getSelection().removeAllRanges()
        : document.selection.empty();
    };
    // 
    const initContent = () => {
      let pageContentArr = [];
      swh1.forEach((item) => {
        pageContentArr.push(item.pageContent);
      });
      data.longTextContent = pageContentArr.join(''); // 
    };
    initContent();
    // 
    const startQuery = () => {
      const absQueryKeyWord = data.queryKeyWord.trim(); //
      if (absQueryKeyWord) {
        // 
        data.queryKeyWordArr = absQueryKeyWord
          .split(' ')
          .filter((item) => item.length); //
        data.queryKeyWordReg = data.queryKeyWordArr.join('|'); // | 
        // 
        if (
          data.oldQueryKeyWordReg === data.queryKeyWordReg &&
          data.oldQueryType === data.queryType
        )
          return;
        // 
        data.oldQueryType = data.queryType; //
        data.oldQueryKeyWordReg = data.queryKeyWordReg; //
        resetData(); // ,
        data.isLoading = true; //
        setTimeout(() => {
          console.time('');
          setHighlight(); // ,loading
          console.timeEnd('');
        }, 0);
      } else {
        resetData(); //  
      }
    };
    // 
    const emptyAllHigh = () => {
      // 
      let temporarySpanAll = Array.from(
        document.getElementsByClassName('temporarySpan')
      );
      temporarySpanAll.forEach((itemNode) => {
        // k++ 7196 308.1728515625 ms temporarySpanclass="swhKey"table
        itemNode.innerHTML = itemNode.innerHTML.replace(
          /<span class="swhKey"(?:.*?)>([\s\S]*?)<\/span>/g,
          '$1'
        );
        // 17196 10559.537841796875 ms
        // itemNode.parentNode.replaceChild(
        //   document.createTextNode(itemNode.innerText),
        //   itemNode
        // );
      });
      // 2table 7196 222.956787109375 ms
      // data.centerEle.innerHTML = data.longTextContent; //
      // copyTable(); //
      // 3
    };
    // 
    const resetData = () => {
      if (data.isHaveKey) {
        setTimeout(() => {
          console.time('');
          emptyAllHigh(); //   
          console.timeEnd('');
          data.oldEle = null; //
          data.catalogue = []; //
          data.keyWordIndex = []; //
          data.Knum = 0; //
          data.Ktotal = 0; //
          data.listNumId = 0; //
          data.isHaveKey = false; //
        }, 0);
      }
    };
    //
    const switchHighlight = (type) => {
      // 1
      if (data.Ktotal < 1) return;
      // 
      if (type === 'next') {
        //
        data.Knum = data.Knum === data.Ktotal ? 1 : ++data.Knum;
      } else if (type === 'prev') {
        //
        data.Knum = data.Knum <= 1 ? data.Ktotal : --data.Knum;
      } else {
        //  idid
        const targetID = type.target.children[0] || type.target;
        // 
        data.Knum = targetID.id.split('_')[1];
      }
      // id
      clickPieceList();
    };
    //
    const clickPieceList = () => {
      let ele = document.querySelector(`#key_${data.Knum}`);
      // 
      if (data.oldEle) {
        data.oldEle.style.backgroundColor = 'transparent'; // 
        data.oldEle.style.color = data.keyWordBCol; // 
      }
      // 
      ele.style.backgroundColor = data.keyWordBCol; // 
      ele.style.color = data.keyWordSCol; // 
      // 
      ele.scrollIntoView(true);
      // ,
      data.oldEle = ele;
    };
    //******    dom
    const forDomGetHigh = (root) => {
      //===================  dom (100  k++6000ms-60ms
      for (let i = 0; i < root.childNodes.length; i++) {
        // 
        const currentNode = root.childNodes[i];
        // 
        const nodeText = currentNode.nodeValue;
        if (currentNode.nodeType == 1) {
          // 
          forDomGetHigh(currentNode);
        } else if (currentNode.nodeType == 3) {
          //    \n,table
          if (
            data.queryType === RETRIEVAL_TYPE.sentence &&
            !nodeText.includes('\n')
          ) {
            // (  )
            withSentence(currentNode, nodeText);
          } else {
            //  
            //     replace
            // 
            const highKeyStr = nodeTextHigh(nodeText);
            //
            // *****
            if (highKeyStr !== nodeText) {
              replaceNode(currentNode, highKeyStr);
            }
          }
        }
      }
      //===================

      // //     
      // ================   dom
      // const children = root.children;
      // // 
      // if(children.length){
      //   for (var i = 0; i < children.length;i++){
      //    children[i].children&&forDomGetHigh(children[i])
      //   }
      // }else {
      //     // KW: 
      //     // k1: 
      //     // 
      //     //         |   | | data.queryKeyWordReg
      //     root.innerHTML= root.innerText.replace(new RegExp(data.queryKeyWordReg, "g"), (KW) => {
      //         //
      //         data.Ktotal++;    //
      //         return `<span id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
      //     });
      // }
      // =================
      // (html)
    };
    //
    const withSentence = (currentNode, nodeText) => {
      //   1:    aaaaaasahdddd;ccccc;hhhhsah
      const sentenceArr = nodeText.split(new RegExp('[;]'));
      //   1: [aaaaaa,sahdddd ,hhhhsah]
      const satisfySentenceArr = sentenceArr.filter((itemSentence) => {
        // 
        if (!itemSentence) return false;
        // 
        const bool = data.queryKeyWordArr.every((item) => {
          return itemSentence.includes(item);
        });
        // 
        if (bool) return itemSentence;
      });
      // 
      satisfySentenceArr.forEach((itemSen) => {
        // k
        data.catalogue.push({
          content: itemSen
        });
        // 
        const highKeyStr = nodeTextHigh(itemSen);
        // 
        nodeText = nodeText.replace(itemSen, highKeyStr);
      });
      replaceNode(currentNode, nodeText);
    };
    //
    const nodeTextHigh = (t) => {
      return t.replace(new RegExp(data.queryKeyWordReg, 'g'), (KW) => {
        data.Ktotal++; //
        // class="swhKey" 
        return `<span class="swhKey" id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
      });
    };
    // 
    const replaceNode = (currentNode, highKeyStr) => {
      // spanhtml<span>
      const temporarySpan = document.createElement('span');
      temporarySpan.innerHTML = highKeyStr; //
      temporarySpan.className = 'temporarySpan'; //class()
      // 
      currentNode.parentNode.replaceChild(temporarySpan, currentNode);
      /**
       *  <p></p>       "" 
       * <p>
       *              temporarySpan
       *             <span class="temporarySpan">
       *               ""
       *              <span class="swhKey" id="key_1" style="color: rgb(255, 255, 255); padding: 0px 5px; background-color: red;"></span>
       *               ""
       *             </span>
       *            </p>
       *  */
    };

    // k++ dom
    const forDomGetHighFilter = (root) => {
      const children = root.children;
      const oneEleText = []; //
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        for (var i = 0; i < children.length; i++) {
          //   true
          const bool = data.queryKeyWordArr.some((item) => {
            return children[i].innerText.includes(item);
          });
          // 
          if (bool) {
            oneEleText.push(children[i]);
          }
        }
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        for (var i = 0; i < children.length; i++) {
          //   true
          const bool = data.queryKeyWordArr.every((item) => {
            return children[i].innerText.includes(item);
          });
          // 
          if (bool) {
            // k ,id
            if (data.queryType === RETRIEVAL_TYPE.paragraph) {
              data.catalogue.push({
                content: children[i].innerText
              });
            }
            // 
            oneEleText.push(children[i]);
          }
        }
      }
      //  
      if (oneEleText.length) {
        oneEleText.forEach((item) => {
          forDomGetHigh(item);
        });
      }
    };
    //&&
    const setHighlight = () => {
      // forDomGetHighFilter  forDomGetHigh 
      forDomGetHighFilter(data.centerEle);
      // 
      if (data.Ktotal) {
        data.isHaveKey = true; //
        //  
        console.time('');
        getQueryList();
        console.timeEnd('');
      } else {
        myTips('error', '~');
      }
      data.isLoading = false;
    };
    //  
    const getQueryList = () => {
      // 
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        // ()
        getOriginalIndex();
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        //    
        data.catalogue.forEach((item) => {
          item.content = pureListTextHigh(item.content); //
        });
      }
      // 
      switchHighlight('next');
    };
    // 303
    // 1  
    const getOriginalIndex = () => {
      // 
      const regInter = eval(`/${data.queryKeyWordReg}/g`); //
      const regExp = data.textCenter.matchAll(regInter); //
      for (const item of regExp) {
        const keyPObj = {
          kWord: item[0], //
          index: item.index //
        };
        data.keyWordIndex.push(keyPObj); //
      }
      // 30
      getHighIndex();
    };
    // 2 30
    const getHighIndex = () => {
      let handleIndex = [];
      for (let i = 0; i < data.keyWordIndex.length; i++) {
        // 0
        const prevIndex = i
          ? data.keyWordIndex[i - 1].index +
            data.keyWordIndex[i - 1].kWord.length
          : 0;
        const currentIndex = data.keyWordIndex[i].index; //
        //,undefined 
        const nextIndex = data.keyWordIndex[i + 1]?.index;
        //-30
        const initStartIndex = currentIndex - data.keyPLength;
        //  ++30
        const initEndIndex =
          currentIndex + data.keyWordIndex[i].kWord.length + data.keyPLength;
        let handleIndexObj = {
          // 
          startIndex: initStartIndex < prevIndex ? prevIndex : initStartIndex,
          // 
          // undefined
          endIndex: initEndIndex > nextIndex ? nextIndex : initEndIndex
        };
        handleIndex.push(handleIndexObj); // 
      }
      highParagraphHandle(handleIndex); // 
    };
    // 3  
    const highParagraphHandle = (SEindexObj) => {
      SEindexObj.forEach((item, index) => {
        //  
        let pCenter = data.textCenter.slice(item.startIndex, item.endIndex);
        // id
        const itemKeyWordObj = {
          content: pureListTextHigh(pCenter) //
        };
        //k 
        data.catalogue.push(itemKeyWordObj);
      });
    };
    //
    const pureListTextHigh = (itemContent) => {
      return itemContent.replace(
        new RegExp(data.queryKeyWordReg, 'g'),
        (KW, index) => {
          data.listNumId++;
          //  console.log(index);   
          return `<span id="list_${data.listNumId}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`; // 
        }
      );
    };
    // 
    const myTips = (type, text) => {
      proxy.h_tips({
        type: type, //success   error
        msg: text,
        top: 30,
        time: 1000
      });
    };
    return {
      data,
      startQuery,
      switchHighlight
    };
  }
};
</script>

